* {
  margin: 0;
  padding: 0;
}

.App {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}  

.Form {
  margin: 1rem;
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}

.FormCol {
  margin: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
}

.formButton {
  width: 100%;
  height: 1rem;
}

.ClockTable {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

/* clocks' CSS styles */
.Clocks__container {
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid gray;
  border-radius: 0.7rem;
  margin: 0.5rem;
  padding: 1rem;
}

.Clocks__header {
  width: 100%;
  height: 1rem;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.Clocks_delete_button {
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
}

.time__string {
  padding: 0.5rem;
}

.clock {
    box-shadow: inset 0 0 0 0.02em #000;
    display: inline-block;
    border-radius: 0.5em;
    position: relative;
    font-size: 250px;
    overflow: hidden;
    height: 1em;
    width: 100%;
}

.clock::after {
    content: '';
    background-color: #fff;
    border-radius: 0.5em;
    margin-left: -0.5em;
    margin-top: -0.5em;
    position: absolute;
    font-size: 0.01em;
    display: block;
    height: 1em;
    width: 1em;
    left: 50%;
    top: 50%;
}

.clock__hand-container, .clock__hand, .clock__stroke {
    margin-left: -0.5em;
    margin-top: -0.5em;
    font-size: inherit;
    position: absolute;
    display: block;
    height: 1em;
    width: 1em;
    left: 50%;
    top: 50%;
}

.clock__stroke::after {
    content: "";
    border-radius: 0 0 0.02em 0.02em;
    background-color: #000;
    margin-left: -0.01em;
    position: absolute;
    display: block;
    height: 0.05em;
    width: 0.02em;
    top: 0.02em;
    left: 50%;
}

.clock__stroke--small::after {
    content: "";
    border-radius: 0 0 0.0125em 0.0125em;
    height: 0.025em;
    width: 0.015em;
}

