.clock__hand--hour::after {
    content: "";
    border-radius: 0.015em 0.015em 0.01em 0.01em;
    background-color: #000;
    margin-bottom: -0.02em;
    margin-left: -0.025em;
    font-size: inherit;
    position: absolute;
    display: block;
    height: 0.25em;
    width: 0.05em;
    bottom: 50%;
    left: 50%;
}

.clock__hand--minute::after {
    content: "";
    border-radius: 0.012em 0.012em 0.01em 0.01em;
    background-color: #000;
    margin-bottom: -0.02em;
    margin-left: -0.015em;
    font-size: inherit;
    position: absolute;
    display: block;
    height: 0.4em;
    width: 0.03em;
    bottom: 50%;
    left: 50%;
}

.clock__hand--second::after {
    content: "";
    border-radius: 0.01em 0.01em 0.005em 0.005em;
    background-color: #f00;
    margin-bottom: -0.02em;
    margin-left: -0.005em;
    font-size: inherit;
    position: absolute;
    display: block;
    height: 0.46em;
    width: 0.01em;
    bottom: 50%;
    left: 50%;
}

.clock__hand {
  margin-left: -0.5em;
  margin-top: -0.5em;
  font-size: inherit;
  position: absolute;
  display: block;
  height: 1em;
  width: 1em;
  left: 50%;
  top: 50%;
}
.clock__hand--hour::after {
  content: "";
  border-radius: 0.015em 0.015em 0.01em 0.01em;
  background-color: #000;
  margin-bottom: -0.02em;
  margin-left: -0.025em;
  font-size: inherit;
  position: absolute;
  display: block;
  height: 0.25em;
  width: 0.05em;
  bottom: 50%;
  left: 50%;
}

/* clocks' animation CSS styles */
.clock__hand--hour {
  animation: clock-hand-rotate 43200s linear infinite;
}
.clock__hand--minute {
  animation: clock-hand-rotate 3600s linear infinite;
}
.clock__hand--second {
  animation: clock-hand-rotate 60s linear infinite;
}
@keyframes clock-hand-rotate {
  from {
      transform: rotate(0deg)
  }
  to {
      transform: rotate(360deg)
  }
}
